import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import posts from './views/nav1/posts.vue'
import image from './views/nav1/image.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import Page6 from './views/nav3/Page6.vue'
import echarts from './views/charts/echarts.vue'

let routes = [{
    path: '/login',
    component: Login,
    name: '',
    hidden: true
  },
  {
    path: '/404',
    component: NotFound,
    name: '',
    hidden: true
  },
  {
    path: '/',
    component: Home,
    leaf: true, //
    name: '',
    children: [{
      path: '/table',
      component: Table,
      name: ''
    }]
  },
  {
    path: '/',
    component: Home,
    leaf: true, //
    name: '',
    children: [{
      path: '/user',
      component: user,
      name: ''
    }]
  },
  {
    path: '/',
    component: Home,
    leaf: true, //
    name: '',
    children: [{
      path: '/posts',
      component: posts,
      name: ''
    }]
  },
  {
    path: '/',
    component: Home,
    leaf: true, //
    name: '',
    children: [{
      path: '/image',
      component: image,
      name: ''
    }]
  },
  //{ path: '/main', component: Main },
  // {
  //     path: '/',
  //     component: Home,
  //     name: '',
  //     iconCls: 'el-icon-message',//class
  //     children: [
  //         { path: '/main', component: Main, name: '', hidden: true },
  //         { path: '/table', component: Table, name: '' },
  //         // { path: '/form', component: Form, name: 'Form' },
  //         { path: '/user', component: user, name: '' },
  //         { path: '/posts', component: posts, name: '' },
  //         { path: '/image', component: image, name: '' },
  //     ]
  // },
  // {
  //     path: '/',
  //     component: Home,
  //     name: '',
  //     iconCls: 'fa fa-id-card-o',
  //     children: [
  //         { path: '/page4', component: Page4, name: '4' },
  //         { path: '/page5', component: Page5, name: '5' }
  //     ]
  // },
  // {
  //     path: '/',
  //     component: Home,
  //     name: '',
  //     iconCls: 'fa fa-address-card',
  //     leaf: true,//
  //     children: [
  //         { path: '/page6', component: Page6, name: '' }
  //     ]
  // },
  // {
  //     path: '/',
  //     component: Home,
  //     name: 'Charts',
  //     iconCls: 'fa fa-bar-chart',
  //     children: [
  //         { path: '/echarts', component: echarts, name: 'echarts' }
  //     ]
  // },
  {
    path: '*',
    hidden: true,
    redirect: {
      path: '/404'
    }
  }
];

export default routes;



